.question-bank .content-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
  margin-bottom: 20px;
  padding-left: 10px;
}
.question-bank .content-top .paper-common-btn {
  flex: 1;
}
.question-bank .content-top .paper-common-btn .ant-btn {
  border-radius: 2px !important;
  margin-right: 20px;
}
.question-bank .paper-common-btn .ant-btn {
  border: none;
  transition: none;
}
.question-bank .paper-common-btn .ant-btn-primary {
  width: auto !important;
}
.question-bank .paper-right-menu {
  border-radius: 20px;
  background: #fff !important;
  padding: 15px;
}
.question-bank .paper-right-menu .paper-common .paper-common-btn .ant-btn {
  border: none;
  background: none;
}
.question-bank .paper-right-menu .paper-common .paper-common-btn .ant-btn.ant-btn-primary {
  border-radius: 5px !important;
  height: 24px;
  line-height: 24px;
}
.question-bank .paper-right-menu .paper-common .paper-common-btn .ant-btn.ant-btn-primary span {
  color: #fff !important;
}
.question-bank .paper-right-menu .paper-common .paper-common-btn .ant-btn span {
  font-size: 14px;
  color: #000;
  font-weight: 400;
}
.question-bank .paper-right-menu .paper-common .ant-radio-button-wrapper {
  margin: 0 2px 5px 0;
  color: #888;
  background: none;
  border: none;
}
.question-bank .paper-right-menu .paper-common .ant-radio-button-wrapper::before {
  width: 0;
}
.question-bank .paper-right-menu .paper-common .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background: #27CDA2;
  border-radius: 5px;
  height: 24px;
  line-height: 24px;
  color: #fff;
}
.question-bank .paper-right-menu .paper-common .actives {
  color: #2cbde6;
  border-color: #2cbde6;
}
.question-bank .ant-btn-link {
  color: #000 !important;
}
.question-bank .ant-btn span {
  font-size: 13px;
}
.question-bank .paper-common-btn .ant-btn {
  border: 1px solid #ccc;
  min-width: 102px !important;
}
.question-bank .paper-common-btn .ant-btn:hover {
  border: 1px solid #3E78ED;
  color: #3E78ED;
}
.question-bank .paper-common-btn .ant-btn-primary {
  background: #3E78ED !important;
  min-width: 102px !important;
}
.question-bank .paper-common-btn .ant-btn-primary {
  color: #fff !important;
}
.teach-main-content-lh {
  height: calc(100vh - 195px);
  padding: 0;
}
.teach-main-content-lh .paper-right-top {
  width: 100%;
}
.teach-main-content-lh .question-tab {
  height: 100%;
}
.btn-lan .ant-btn {
  background: #3E78ED !important;
  color: #fff;
}
